import queryString from 'query-string';

const parsed = queryString.parse(location.search);

export const source = parsed.utm_source;
export const medium = parsed.utm_medium;
export const campaign = parsed.utm_campaign;
export const content = parsed.utm_content;
export const term = parsed.utm_term;

export default {
  source,
  medium,
  campaign,
  content,
  term,
};
