export function makeLink(text, href) {
  return `<a class='tw-underline tw-text-white hover:tw-text-gray-400' href='${href}'>
    ${text}
  </a>`;
}

export function updateAnnouncementBar(geoData) {
  const announcement = document.querySelector('#announce');

  if (geoData.country_code === 'CA' || geoData.country_code === 'US') {
    if (announcement.innerText.startsWith('Free Shipping')) {
      const appendedText = geoData.country_code === 'CA'
        ? '$95'
        : '$70';

      announcement.innerText = `${announcement.innerText} ${appendedText}`;
    }
  } else {
    announcement.innerText = `${announcement.innerText} $70`;
  }

  announcement.parentElement.parentElement.classList.remove('tw-hide');
}
